:root {
    --primary-color: #3f4e52;
    --bg-color: #e5e5e5;
    --bg-f8-color: #f8f8f8;
    --white-color: #ffffff;
    --Gray-1-color: #333333;
    --Gray-2-color: #4f4f4f;
    --Gray-3-color: #828282;
    --Gray-4-color: #d1d1d6;
    --Gray-5-color: #e0e0e0;
    --Gray-6-color: #f2f2f2;
    --Green-1-color: #38bc94;
    --Green-2-color: #7fd4ba;
    --f5fffc-color: #f5fffc;
    --black-color: #000000;
    --balck-2-color: #2f3c40;
    --bdbdbd-color: #bdbdbd;
    --daf2eb-color: #daf2eb;
}

html {
    --antd-wave-shadow-color: transprent;
}

body,
#root,
.App {
    height: 100%;
}

body {
    font-family: "Inter", sans-serif !important;
    font-size: 14px;
}

a,
button,
.ant-btn {
    transition: 0.3s ease all;
    text-shadow: none !important;
}

a:focus,
button:focus,
.ant-btn:focus,
a:hover,
button:hover {
    outline: none;
    text-decoration: none !important;
    box-shadow: none !important;
    text-shadow: none !important;
}

.ant-btn:hover {
    background: var(--Gray-1-color);
    color: var(--white-color);
}

.flex-align-center {
    display: flex;
    align-items: center;
}

.flex-align-center-center {
    display: flex;
    align-items: center;
    justify-content: center;
}

.flex-align-center-between {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.hide {
    display: none !important;
}

